"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

interface StripeCheckoutProps {
  planType: "monthly" | "yearly";
  planName: string;
  price: string;
  priceId: string;
  features: string[];
}

export function StripeCheckout({ planType, planName, price, priceId, features }: StripeCheckoutProps) {
  const [isLoading, setIsLoading] = useState(false);

  const handleCheckout = async () => {
    setIsLoading(true);

    try {
      const response = await fetch("/api/stripe/checkout", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          priceId: priceId,
          planType: planType,
        }),
      });

      const { sessionId, error } = await response.json();

      if (error) {
        console.error("Erreur cr√©ation session:", error);
        alert("Erreur lors de la cr√©ation de la session de paiement");
        return;
      }

      // Redirection vers Stripe Checkout
      const stripe = (window as any).Stripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY);
      const { error: stripeError } = await stripe.redirectToCheckout({
        sessionId: sessionId,
      });

      if (stripeError) {
        console.error("Erreur redirection Stripe:", stripeError);
        alert("Erreur lors de la redirection vers le paiement");
      }
    } catch (error) {
      console.error("Erreur checkout:", error);
      alert("Une erreur est survenue");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Card className={`relative ${planType === "yearly" ? "border-emerald-500 shadow-lg" : "border-gray-200"}`}>
      {planType === "yearly" && (
        <div className="absolute -top-3 left-1/2 transform -translate-x-1/2">
          <Badge className="bg-emerald-600 text-white px-4 py-1">
            üí∞ √âconomisez 84‚Ç¨
          </Badge>
        </div>
      )}
      
      <CardHeader className="text-center pb-4">
        <CardTitle className="text-2xl font-bold text-emerald-700">
          {planName}
        </CardTitle>
        <CardDescription className="text-gray-600">
          {planType === "monthly" ? "Facturation mensuelle" : "Facturation annuelle"}
        </CardDescription>
        <div className="mt-4">
          <span className="text-4xl font-bold text-emerald-700">{price}</span>
          <span className="text-gray-600 ml-2">
            {planType === "monthly" ? "/mois" : "/an"}
          </span>
        </div>
        {planType === "yearly" && (
          <div className="text-sm text-emerald-600 mt-2">
            Soit 35‚Ç¨/mois (au lieu de 42‚Ç¨/mois)
          </div>
        )}
      </CardHeader>

      <CardContent className="space-y-4">
        <div className="space-y-3">
          {features.map((feature, index) => (
            <div key={index} className="flex items-center space-x-3">
              <div className="w-5 h-5 bg-emerald-100 rounded-full flex items-center justify-center">
                <span className="text-emerald-600 text-sm">‚úì</span>
              </div>
              <span className="text-gray-700">{feature}</span>
            </div>
          ))}
        </div>

        <Button
          onClick={handleCheckout}
          disabled={isLoading}
          className={`w-full mt-6 ${
            planType === "yearly"
              ? "bg-emerald-600 hover:bg-emerald-700"
              : "bg-blue-600 hover:bg-blue-700"
          } text-white`}
        >
          {isLoading ? (
            <div className="flex items-center space-x-2">
              <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
              <span>Redirection...</span>
            </div>
          ) : (
            `S'abonner maintenant`
          )}
        </Button>

        <div className="text-center text-xs text-gray-500 mt-4">
          <div>üîí Paiement s√©curis√© par Stripe</div>
          <div>Annulation possible √† tout moment</div>
        </div>
      </CardContent>
    </Card>
  );
}
